* {
  color: #eeeeee;
  cursor: none;
}

html {
  background: linear-gradient(60deg,#222831, #31363f );
  height: 100vh;
  padding-left: 5%;
}

.kursor {
  background-color: transparent; /* Убирает фон у курсора */
}

h1 {
  font-family: nunito;
  font-weight: 800;
  font-style: italic;
  text-transform: uppercase;
}


/* #region fonts */
/* #region suse */
@font-face {
  font-family: Suse;
  src: url('./fonts/suse/SUSE-Regular.ttf') format('truetype'); /* Убедитесь, что файл существует */
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Suse;
  src: url('./fonts/suse/SUSE-Medium.ttf') format('truetype');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Suse;
  src: url('./fonts/suse/SUSE-SemiBold.ttf') format('truetype');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Suse;
  src: url('./fonts/suse/SUSE-Bold.ttf') format('truetype');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Suse;
  src: url('./fonts/suse/SUSE-ExtraBold.ttf') format('truetype');
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}
/* #endregion */
